import bulmaSteps from '../../static/js/bulma-steps.min.js'
import utils from '../../common/utils'
import PostTweets from '~/components/PostTweets'

export default {
  components: {
    PostTweets
  },
  data() {
    return {
      publishing: false, // 当前是否正处于发布中...
      captchaId: '',
      captchaUrl: '',
      captchaCode: '',
      maxWordCount: 1000,
      showUploader: false,
      maxImageCount: 9,
      images: [],
      postForm: {
        nodeId: 1, // love node id
        title: '',
        me_city: '',
        me_age: '',
        me_height: '',
        me_gender: '男', // 0: boy; 1: girl
        me_edu: '', // 学历
        me_work_city: '',
        me_work_company: '', // 工作单位
        me_hobby: '', // 兴趣爱好
        me_wechat: '', //
        me_other_info: '', // 择偶观
        me_photos: [], // 个人照片

        he_city: '无', // 籍贯要求
        he_age: '', // 年龄要求
        he_height: '', // 身高
        he_gender: 1,
        he_edu: '',
        he_work_city: '',
        he_other_info: ''
      }
    }
  },
  mounted() {
    bulmaSteps.attach('.steps', {
      beforeNext: (id) => {
        switch (id) {
          // first step
          case 0:
            // must return an array of errors
            // if empty it goes to next step
            // if not empty it triggers onError with the first item of array
            return validateFirstStep()

          // second step
          case 1:
            return validateTwoStep()

          case 2:
            if (utils.isEmpty(this.postForm.title)) {
              this.$toast.error('请按照要求填满所有信息')
              return [{}]
            }
            break
        }
      },
      onError: (error) => {
        console.log(error)
      },
      onFinish: (msg) => {
        console.log(msg)
      }
    })

    const validateFirstStep = () => {
      const errors = []
      // if (validationRuleNotVerified) {
        console.log('form:' + JSON.stringify(this.postForm))
         console.log('images length:' + this.images.length)
      if (
        utils.isEmpty(this.postForm.me_city) ||
        utils.isEmpty(this.postForm.me_age) ||
        utils.isEmpty(this.postForm.me_edu) ||
        utils.isEmpty(this.postForm.me_height) ||
        utils.isEmpty(this.postForm.me_gender) ||
        utils.isEmpty(this.postForm.me_work_city) ||
        utils.isEmpty(this.postForm.me_work_company) ||
        utils.isEmpty(this.postForm.me_hobby) ||
        utils.isEmpty(this.postForm.me_wechat) ||
        utils.isEmpty(this.postForm.me_other_info) ||
        this.images.length === 0
      ) {
        this.$toast.error('请按照要求填满所有信息及照片')
        errors.push('')
      }

      // }
      // if (otherValidationRuleNotVerified) {
      //   errors.push('Other validation rule not verified')
      // }
      return errors
    }

    const validateTwoStep = () => {
      const errors = []
      // if (validationRuleNotVerified) {
      if (
        utils.isEmpty(this.postForm.he_city) ||
        utils.isEmpty(this.postForm.he_height) ||
        utils.isEmpty(this.postForm.he_age) ||
        utils.isEmpty(this.postForm.he_edu) ||
        utils.isEmpty(this.postForm.he_work_city) ||
        utils.isEmpty(this.postForm.he_other_info)
      ) {
        this.$toast.error('请按照要求填满所有信息')
        errors.push('')
      }

      // }
      // if (otherValidationRuleNotVerified) {
      //   errors.push('Other validation rule not verified')
      // }
      return errors
    }
  },
  methods: {
    configData() {
      return {
        title: this.postForm.title,
        me_city: this.postForm.me_city,
        me_age: this.postForm.me_age,
        me_height: this.postForm.me_height,
        me_gender: this.postForm.me_gender, // 0: boy; 1: girl
        me_edu: this.postForm.me_edu, // 学历
        me_work_city: this.postForm.me_work_city,
        me_work_company: this.postForm.me_work_company, // 工作单位
        me_hobby: this.postForm.me_hobby, // 兴趣爱好
        me_wechat: this.postForm.me_wechat, //
        me_other_info: this.postForm.me_other_info, // 择偶观
        me_photos: this.images.toString(), // 个人照片

        he_city: this.postForm.he_city, // 籍贯要求
        he_age: this.postForm.he_age, // 年龄要求
        he_height: this.postForm.he_height, // 身高
        he_gender: this.postForm.he_gender,
        he_edu: this.postForm.he_edu,
        he_work_city: this.postForm.he_work_city,
        he_other_info: this.postForm.he_other_info
      }
    },
    async submit() {
      if (this.publishing) {
        return
      }

      if (!this.postForm.title) {
        this.$toast.error('请输入标题')
        return
      }

      if (!this.postForm.nodeId) {
        this.$toast.error('请选择节点')
        return
      }

      this.publishing = true

      try {
        const topic = await this.$axios.post('/api/topic/create', {
          city: utils.getCity(),
          captchaId: this.captchaId,
          captchaCode: this.captchaCode,
          nodeId: this.postForm.nodeId,
          title: this.postForm.title,
          imageList: this.images.toString(),
          content: '',
          extraData: JSON.stringify(this.configData()),
          tags: this.postForm.tags ? this.postForm.tags.join(',') : ''
        })
        // this.$refs.mdEditor.clearCache()
        this.$toast.success('提交成功', {
          duration: 1000,
          onComplete() {
            utils.linkTo('/love/' + topic.topicId)
          }
        })
      } catch (e) {
        await this.showCaptcha()
        this.publishing = false
        this.$toast.error('提交失败：' + (e.message || e))
      }
    },
    async onImageUpload(imageList) {
      this.images = imageList
    }
  }
}
